import { call, put } from 'redux-saga/effects';

import request from './request';
import { fetchSearchRequested, fetchSearchSucceeded, fetchSearchFailed } from '../../../actions';
import api from '../../../api';

// eslint-disable-next-line import/prefer-default-export
export function* fetchSearchRequest(text) {
  yield put(fetchSearchRequested(text));

  try {
    const data = { text };
    const result = yield call(request, api.createSearchAction, data);
    const { projects, boards, cards, tasks, labels } = result;
    const action = fetchSearchSucceeded(projects, boards, cards, tasks, labels);
    yield put(action);

    return {
      success: true,
      payload: action.payload,
    };
  } catch (error) {
    const action = fetchSearchFailed(text, error);
    yield put(action);

    return {
      success: false,
      payload: action.payload,
    };
  }
}
