import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { closeModal, copyProject } from '../actions/entry';
import ProjectCopyModal from '../components/ProjectCopyModal';

const mapStateToProps = ({ projectCopyForm: { data: defaultData, isSubmitting } }) => ({
  defaultData,
  isSubmitting,
});

const mapDispatchToProps = (dispatch) =>
  bindActionCreators(
    {
      onCopy: copyProject,
      onClose: closeModal,
    },
    dispatch,
  );

export default connect(mapStateToProps, mapDispatchToProps)(ProjectCopyModal);
