/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable react/sort-comp */
/* eslint-disable react/no-string-refs */
/* eslint-disable react/prop-types */
/* eslint-disable react/destructuring-assignment */
/* eslint-disable camelcase */
/* eslint-disable react/prefer-stateless-function */
/* eslint-disable no-useless-constructor */
/* eslint-disable max-classes-per-file */
import React, { Component } from 'react';
import sizeMe from 'react-sizeme';
import { DATA_CONTAINER_WIDTH } from '../../Const';
import DataTask from './DataTask';
import DateHelper from '../../helpers/DateHelper';
import Config from '../../helpers/config/Config';

export class DataRow extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    const class_row = `timeLine-main-data-row ${this.props.type}`;
    return (
      <div
        className={class_row}
        style={{
          ...Config.values.dataViewPort.rows.style,
          top: this.props.top,
          height: this.props.itemheight,
        }}
      >
        {this.props.children}
      </div>
    );
  }
}

export class DataViewPort extends Component {
  constructor(props) {
    super(props);
    this.childDragging = false;
  }

  getContainerHeight(rows) {
    const new_height = rows > 0 ? rows * this.props.itemheight : 10;
    return new_height;
  }

  onChildDrag = (dragging) => {
    this.childDragging = dragging;
  };

  renderRows = () => {
    const result = [];
    for (let i = this.props.startRow; i < this.props.endRow + 1; i += 1) {
      const item = this.props.data[i];
      if (!item) break;
      // FIXME PAINT IN BOUNDARIES
      let new_position = 0;
      if (item.start !== '') {
        const inputValue = new Date(item.start);
        const datestring = `${inputValue.getFullYear()},${
          inputValue.getMonth() + 1
        },${inputValue.getDate()}`;
        const inputTime = new Date(datestring);
        new_position = DateHelper.dateToPixel(
          inputTime,
          this.props.nowposition,
          this.props.dayWidth,
        );
      }
      let new_width = 0;
      if (item.end !== '') {
        const inputValue = new Date(item.end);
        inputValue.setDate(inputValue.getDate() + 1);
        const datestring = `${inputValue.getFullYear()},${
          inputValue.getMonth() + 1
        },${inputValue.getDate()}`;
        const inputTime = new Date(datestring);
        new_width =
          DateHelper.dateToPixel(inputTime, this.props.nowposition, this.props.dayWidth) -
          new_position;
      }

      result.push(
        <DataRow
          key={i}
          label={item.name}
          type={item.type}
          top={i * this.props.itemheight}
          left={20}
          itemheight={this.props.itemheight}
        >
          <DataTask
            item={item}
            label={item.name}
            nowposition={this.props.nowposition}
            dayWidth={this.props.dayWidth}
            color={item.color}
            left={new_position}
            width={new_width}
            height={this.props.itemheight}
            onChildDrag={this.onChildDrag}
            isSelected={this.props.selectedItem === item}
            onSelectItem={this.props.onSelectItem}
            onStartCreateLink={this.props.onStartCreateLink}
            onFinishCreateLink={this.props.onFinishCreateLink}
            onTaskChanging={this.props.onTaskChanging}
            onUpdateTask={this.props.onUpdateTask}
          >
            {' '}
          </DataTask>
        </DataRow>,
      );
    }
    return result;
  };

  doMouseDown = (e) => {
    if (e.button === 0 && !this.childDragging) {
      this.props.onMouseDown(e);
    }
  };

  doMouseMove = (e) => {
    this.props.onMouseMove(e, this.refs.dataViewPort);
  };

  doTouchStart = (e) => {
    if (!this.childDragging) {
      this.props.onTouchStart(e);
    }
  };

  doTouchMove = (e) => {
    this.props.onTouchMove(e, this.refs.dataViewPort);
  };

  componentDidMount() {
    this.refs.dataViewPort.scrollLeft = 0;
  }

  render() {
    if (this.refs.dataViewPort) {
      this.refs.dataViewPort.scrollLeft = this.props.scrollLeft;
      this.refs.dataViewPort.scrollTop = this.props.scrollTop;
    }

    const height = this.getContainerHeight(this.props.data.length);
    return (
      <div
        ref="dataViewPort"
        id="timeLinedataViewPort"
        className="timeLine-main-data-viewPort"
        onMouseDown={this.doMouseDown}
        onMouseMove={this.doMouseMove}
        onMouseUp={this.props.onMouseUp}
        onMouseLeave={this.props.onMouseLeave}
        onTouchStart={this.doTouchStart}
        onTouchMove={this.doTouchMove}
        onTouchEnd={this.props.onTouchEnd}
        onTouchCancel={this.props.onTouchCancel}
      >
        <div
          className="timeLine-main-data-container"
          style={{ height, width: DATA_CONTAINER_WIDTH, maxWidth: DATA_CONTAINER_WIDTH }}
        >
          {this.renderRows()}
        </div>
      </div>
    );
  }
}

export default sizeMe({ monitorWidth: true, monitorHeight: true })(DataViewPort);
