import ActionTypes from '../../constants/ActionTypes';

const initialState = {
  data: {
    copy_project_id: '',
    name: '',
  },
  isSubmitting: false,
};

export default (state = initialState, { type, payload }) => {
  switch (type) {
    case ActionTypes.PROJECT_COPY:
      return {
        ...state,
        data: {
          ...state.data,
          ...payload.data,
        },
      };
    case ActionTypes.PROJECT_COPY_REQUESTED:
      return {
        ...state,
        isSubmitting: true,
      };
    case ActionTypes.PROJECT_COPY_SUCCEEDED:
      return initialState;
    case ActionTypes.PROJECT_COPY_FAILED:
      return {
        ...state,
        isSubmitting: false,
      };
    default:
      return state;
  }
};
