import React, { Component } from 'react';
import ContentEditable from '../common/ContentEditable';
import Label from '../../../../Label';
import User from '../../../../User';

export class VerticalLine extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return <div className="timeLine-main-data-verticalLine" style={{ left: this.props.left }} />;
  }
}

export class TaskRow extends Component {
  constructor(props) {
    super(props);
  }

  onChange = (value) => {
    if (this.props.onUpdateTask) {
      this.props.onUpdateTask(this.props.item, { name: value });
    }
  };

  render() {
    const class_row = `timeLine-side-task-row ${this.props.type}`;
    return (
      <div
        className={class_row}
        style={{
          top: this.props.top,
          height: this.props.itemheight,
        }}
        onClick={(__e) => this.props.onSelectItem(this.props.item)}
      >
        {this.props.nonEditable ? (
          <div tabIndex={this.props.index} style={{ width: '100%' }}>
            {this.props.column == 'labels' || this.props.column == 'member' ? (
              <div className="list-item-wrapper">
                {this.props.column == 'labels' ? (
                  <div className="list-labels">
                    {this.props.label.length > 0 && (
                      <span className="labels">
                        {this.props.label.map((label) => (
                          <Label name={label[0]} color={label[1]} size="tiny" />
                        ))}
                      </span>
                    )}
                  </div>
                ) : (
                  <div className="list-members">
                    {this.props.label.length > 0 && (
                      <span className="members">
                        {this.props.label.map((label) => (
                          <User name={label[0]} avatarUrl={label[1]} size="small" />
                        ))}
                      </span>
                    )}
                  </div>
                )}
              </div>
            ) : (
              <div title={this.props.tooltip}>{this.props.label}</div>
            )}
          </div>
        ) : (
          <ContentEditable
            value={this.props.label}
            index={this.props.index}
            onChange={this.onChange}
          />
        )}
      </div>
    );
  }
}

export default class TaskList extends Component {
  constructor(props) {
    super(props);
  }

  getContainerStyle(rows) {
    const new_height = rows > 0 ? rows * this.props.itemheight : 10;
    return { height: new_height };
  }

  renderTaskRow(data, column) {
    const result = [];
    for (let i = this.props.startRow; i < this.props.endRow + 1; i += 1) {
      const item = data[i];
      if (!item) break;
      const lable_data = () => {
        switch (column) {
          case 'labels':
            return item.labels;
          case 'member':
            return item.member;
          case 'description':
            return item.description;
          case 'start':
            if (item.start != '') {
              return `${item.start.getDate()}-${
                item.start.getMonth() + 1
              }-${item.start.getFullYear()}`;
            }
            return;
          case 'end':
            if (item.type == 'card') {
              if (item.end != '') {
                return `${item.end.getDate()}-${item.end.getMonth() + 1}-${item.end.getFullYear()}`;
              }
              return `${item.start.getDate()}-${
                item.start.getMonth() + 1
              }-${item.start.getFullYear()}`;
            }
            return;
          default:
            return item.name;
        }
      };
      const tooltip_data = () => {
        switch (column) {
          case 'name':
            return item.description;
          default:
        }
      };
      result.push(
        <TaskRow
          key={i}
          index={i}
          item={item}
          label={lable_data()}
          tooltip={tooltip_data()}
          type={item.type}
          column={column}
          top={i * this.props.itemheight}
          itemheight={this.props.itemheight}
          isSelected={this.props.selectedItem == item}
          onUpdateTask={this.props.onUpdateTask}
          onSelectItem={this.props.onSelectItem}
          nonEditable={this.props.nonEditable}
        />,
      );
    }
    return result;
  }

  doScroll = () => {
    this.props.onScroll(this.refs.taskViewPort.scrollTop);
  };

  render() {
    const data = this.props.data ? this.props.data : [];
    this.containerStyle = this.getContainerStyle(data.length);
    return (
      <div className="timeLine-sidebar-wrapper">
        <div className="timeLine-cart">
          <div ref="taskViewPort" className="timeLine-side-task-viewPort" onScroll={this.doScroll}>
            <div className="timeLine-side-task-container" style={this.containerStyle}>
              {this.renderTaskRow(data, 'name')}
            </div>
          </div>
        </div>
        <div className="timeLine-label">
          <div ref="taskViewPort" className="timeLine-side-task-viewPort" onScroll={this.doScroll}>
            <div className="timeLine-side-task-container" style={this.containerStyle}>
              {this.renderTaskRow(data, 'labels')}
            </div>
          </div>
        </div>
        <div className="timeLine-member">
          <div ref="taskViewPort" className="timeLine-side-task-viewPort" onScroll={this.doScroll}>
            <div className="timeLine-side-task-container" style={this.containerStyle}>
              {this.renderTaskRow(data, 'member')}
            </div>
          </div>
        </div>
        <div className="timeLine-start">
          <div ref="taskViewPort" className="timeLine-side-task-viewPort" onScroll={this.doScroll}>
            <div className="timeLine-side-task-container" style={this.containerStyle}>
              {this.renderTaskRow(data, 'start')}
            </div>
          </div>
        </div>
        <div className="timeLine-end">
          <div ref="taskViewPort" className="timeLine-side-task-viewPort" onScroll={this.doScroll}>
            <div className="timeLine-side-task-container" style={this.containerStyle}>
              {this.renderTaskRow(data, 'end')}
            </div>
          </div>
        </div>
      </div>
    );
  }
}
