import ActionTypes from '../constants/ActionTypes';

/* Actions */

/* Events */

export const fetchSearchRequested = (text) => ({
  type: ActionTypes.SEARCH_FETCH_REQUESTED,
  payload: {
    text,
  },
});

export const fetchSearchSucceeded = (projects, boards, cards, tasks, labels) => ({
  type: ActionTypes.SEARCH_FETCH_SUCCEEDED,
  payload: {
    projects,
    boards,
    cards,
    tasks,
    labels,
  },
});

export const fetchSearchFailed = (text, error) => ({
  type: ActionTypes.SEARCH_FETCH_FAILED,
  payload: {
    text,
    error,
  },
});
