import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import { Menu, Icon } from 'semantic-ui-react';
import { withPopup } from '../../../lib/popup';
import { Popup } from '../../../lib/custom-ui';
import { useSteps } from '../../../hooks';
import DeleteStep from '../../DeleteStep';
import styles from './ActionsPopup.module.scss';

const StepTypes = {
  DELETE: 'DELETE',
  EDIT_DUE_DATE: 'EDIT_DUE_DATE',
};

const ActionsStep = React.memo(
  ({ userCan, approve, isApproveMenu, onNameEdit, onApprove, onDelete }) => {
    const [t] = useTranslation();
    const [step, openStep, handleBack] = useSteps();

    const handleEditNameClick = useCallback(() => {
      if (userCan.CREATE_TASK) onNameEdit();
    }, [userCan, onNameEdit]);
    const handleApproveReviewClick = useCallback(() => {
      onApprove('approveReview');
    }, [onApprove]);
    const handleApproveRedoClick = useCallback(() => {
      onApprove('approveRedo');
    }, [onApprove]);

    const handleDeleteClick = useCallback(() => {
      if (userCan.CREATE_TASK) openStep(StepTypes.DELETE);
    }, [userCan, openStep]);

    if (step) {
      switch (step.type) {
        case StepTypes.DELETE:
          return (
            <DeleteStep
              title={t('common.deleteTask', {
                context: 'title',
              })}
              content={t('common.areYouSureYouWantToDeleteThisTask')}
              buttonContent={t('action.deleteTask')}
              onConfirm={onDelete}
              onBack={handleBack}
            />
          );
        // case StepTypes.EDIT_DUE_DATE:
        //   return (
        //     <DueDateEditStep
        //       defaultValue={dueDate}
        //       onUpdate={handleDueDateUpdate}
        //       onBack={handleBack}
        //       onClose={onClose}
        //     />
        //   );
        default:
      }
    }

    return (
      <>
        <Popup.Header>
          {t('common.taskActions', {
            context: 'title',
          })}
        </Popup.Header>
        <Popup.Content>
          <Menu secondary vertical className={styles.menu}>
            <Menu.Item className={styles.menuItem} onClick={handleApproveReviewClick}>
              {t('action.approveReview', {
                context: 'title',
              })}
              {approve === 'approveReview' && <Icon name="check" />}
            </Menu.Item>
            {userCan.CREATE_TASK && (
              <Menu.Item className={styles.menuItem} onClick={handleApproveRedoClick}>
                {t('action.approveRedo', {
                  context: 'title',
                })}
                {approve === 'approveRedo' && <Icon name="check" />}
              </Menu.Item>
            )}
            {userCan.CREATE_TASK && !isApproveMenu && (
              <>
                <Menu.Item className={styles.menuItem} onClick={handleEditNameClick}>
                  {t('action.editDescription', {
                    context: 'title',
                  })}
                </Menu.Item>
                <Menu.Item className={styles.menuItem} onClick={handleDeleteClick}>
                  {t('action.deleteTask', {
                    context: 'title',
                  })}
                </Menu.Item>
              </>
            )}
          </Menu>
        </Popup.Content>
      </>
    );
  },
);

ActionsStep.propTypes = {
  userCan: PropTypes.object.isRequired, // eslint-disable-line react/forbid-prop-types
  approve: PropTypes.string.isRequired,
  isApproveMenu: PropTypes.bool,
  onNameEdit: PropTypes.func.isRequired,
  onApprove: PropTypes.func.isRequired,
  onDelete: PropTypes.func.isRequired,
};

ActionsStep.defaultProps = {
  isApproveMenu: false,
};

export default withPopup(ActionsStep);
