import upperFirst from 'lodash/upperFirst';
import React from 'react';
import PropTypes from 'prop-types';
import differenceInCalendarDays from 'date-fns/differenceInCalendarDays';
import classNames from 'classnames';
import { useTranslation } from 'react-i18next';

import styles from './DueDate.module.scss';

const SIZES = {
  TINY: 'tiny',
  SMALL: 'small',
  MEDIUM: 'medium',
};

const FORMATS = {
  tiny: 'longDate',
  small: 'longDate',
  medium: 'longDateTime',
};

const COLORS = [
  { background: '#61bd4f', color: 'white' }, // hoàn thành
  { background: '#e04556', color: 'white' }, // trễ hạn nhiều
  { background: '#f97394', color: 'white' }, // trễ hạn ít
  { background: 'yellow' }, // sắp tới hạn
  {}, // bình thường
];

const DueDate = React.memo(({ value, size, isCompleted, isDisabled, onClick }) => {
  const [t] = useTranslation();

  const getColor = () => {
    const diff = differenceInCalendarDays(value, new Date());

    if (isCompleted) return COLORS[0];
    if (diff < -2) return COLORS[1];
    if (diff < 0) return COLORS[2];
    if (diff < 2) return COLORS[3];
    return COLORS[4];
  };

  const contentNode = (
    <span
      className={classNames(
        styles.wrapper,
        styles[`wrapper${upperFirst(size)}`],
        onClick && styles.wrapperHoverable,
      )}
      style={getColor()}
    >
      {t(`format:${FORMATS[size]}`, {
        value,
        postProcess: 'formatDate',
      })}
    </span>
  );

  return onClick ? (
    <button type="button" disabled={isDisabled} className={styles.button} onClick={onClick}>
      {contentNode}
    </button>
  ) : (
    contentNode
  );
});

DueDate.propTypes = {
  value: PropTypes.instanceOf(Date).isRequired,
  size: PropTypes.oneOf(Object.values(SIZES)),
  isCompleted: PropTypes.bool,
  isDisabled: PropTypes.bool,
  onClick: PropTypes.func,
};

DueDate.defaultProps = {
  size: SIZES.MEDIUM,
  isCompleted: false,
  isDisabled: false,
  onClick: undefined,
};

export default DueDate;
