import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { currentUserSelector, projectsForCurrentUserSelector } from '../selectors';
import { openProjectAddModal, openProjectCopyModal } from '../actions/entry';
import Projects from '../components/Projects';
import Can from '../constants/Can';

const mapStateToProps = (state) => {
  const { role } = currentUserSelector(state);
  const projects = projectsForCurrentUserSelector(state);

  return {
    items: projects,
    userCan: Can[role],
  };
};

const mapDispatchToProps = (dispatch) =>
  bindActionCreators(
    {
      onAdd: openProjectAddModal,
      onCopy: openProjectCopyModal,
    },
    dispatch,
  );

export default connect(mapStateToProps, mapDispatchToProps)(Projects);
