import React, { useCallback, useState } from 'react';
// import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import { Menu, Icon, Sidebar, Button, Header } from 'semantic-ui-react';

import styles from './SidebarMenu.module.scss';

const SidebarMenu = React.memo(() => {
  const [t] = useTranslation();

  const [visible, setVisible] = useState(false);

  const handleToggleSidebar = useCallback(() => {
    setVisible(!visible);
  }, [visible]);

  return (
    <>
      <Button size="tiny" className={styles.button} onClick={handleToggleSidebar}>
        <Icon name="sidebar" />
        {t('action.showMenu')}
      </Button>
      <Sidebar
        as={Menu}
        animation="overlay"
        direction="right"
        onHide={() => setVisible(false)}
        vertical
        visible={visible}
        className={styles.sidebar}
      >
        <Button icon="close" onClick={handleToggleSidebar} className={styles.closeButton} />
        <Header as="h5" attached textAlign="center">
          Menu
        </Header>
        <Menu.Item as="a">
          {t('action.editTitle', {
            context: 'title',
          })}
        </Menu.Item>
        <Menu.Item as="a">
          {t('action.editBackground', {
            context: 'title',
          })}
        </Menu.Item>
        <Menu.Item as="a">{t('action.search')}</Menu.Item>
        <div className={styles.contentModule}>
          <div className={styles.moduleWrapper}>
            <Header as="h4" attached>
              <div className={styles.moduleHeader}>
                <Icon name="list ul" className={styles.moduleIcon} />
                {t('common.actions')}
              </div>
            </Header>
            <div className={styles.wrapper}>...</div>
          </div>
        </div>
      </Sidebar>
    </>
  );
});

// Sidebar.propTypes = {
//   // /* eslint-disable react/forbid-prop-types */
//   // items: PropTypes.array.isRequired,
//   // currentUserIds: PropTypes.array.isRequired,
//   // /* eslint-enable react/forbid-prop-types */
//   // title: PropTypes.string,
//   // onBack: PropTypes.func,
// };

// Sidebar.defaultProps = {
//   // title: 'common.members',
//   // onBack: undefined,
// };

export default SidebarMenu;
