import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { currentUserSelector, notificationsForCurrentUserSelector } from '../selectors';
import {
  deleteNotification,
  logout,
  openUserSettingsModal,
  openUsersModal,
  fetchSearch,
} from '../actions/entry';
import Header from '../components/Header';
import Can from '../constants/Can';

const mapStateToProps = (state) => {
  const currentUser = currentUserSelector(state);
  const notifications = notificationsForCurrentUserSelector(state);
  const { search } = state; // boards, cards, labels

  return {
    notifications,
    user: currentUser,
    userCan: Can[currentUser.role],
    searchResults: search || {},
  };
};

const mapDispatchToProps = (dispatch) =>
  bindActionCreators(
    {
      onUsers: openUsersModal, // TODO: rename
      onNotificationDelete: deleteNotification,
      onUserSettings: openUserSettingsModal,
      onLogout: logout,
      onFullTextSearch: fetchSearch,
    },
    dispatch,
  );

export default connect(mapStateToProps, mapDispatchToProps)(Header);
