import React, { useCallback, useEffect, useImperativeHandle, useRef, useState } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { useTranslation } from 'react-i18next';
import TextareaAutosize from 'react-textarea-autosize';
import { Button, Form, Icon, TextArea } from 'semantic-ui-react';
import DueDateEditPopup from '../../DueDateEditPopup';
import ProjectMembershipsPopup from '../../ProjectMembershipsPopup';
import User from '../../User';

import { useDidUpdate, useToggle } from '../../../lib/hooks';

import { useClosableForm, useForm } from '../../../hooks';

import styles from './Add.module.scss';

const DEFAULT_DATA = {
  name: '',
  dueDate: undefined,
  approve: 'approveTodo',
  userId: undefined,
};

const Add = React.forwardRef(({ children, userCan, allProjectMemberships, onCreate }, ref) => {
  const [t] = useTranslation();
  const [isOpened, setIsOpened] = useState(false);
  const [data, handleFieldChange, setData] = useForm(DEFAULT_DATA);
  const [selectNameFieldState, selectNameField] = useToggle();

  const nameField = useRef(null);

  const handleDueDateUpdate = useCallback(
    (newDueDate) => {
      setData((prevData) => ({
        ...prevData,
        dueDate: newDueDate,
      }));
    },
    [setData],
  );

  const open = useCallback(() => {
    setIsOpened(true);
  }, []);

  const close = useCallback(() => {
    setIsOpened(false);
  }, []);

  const submit = useCallback(() => {
    const cleanData = {
      ...data,
      name: data.name.trim(),
    };

    if (!cleanData.name) {
      nameField.current.ref.current.select();
      return;
    }

    onCreate(cleanData);

    setData(DEFAULT_DATA);
    selectNameField();
  }, [onCreate, data, setData, selectNameField]);

  useImperativeHandle(
    ref,
    () => ({
      open,
      close,
    }),
    [open, close],
  );

  const handleChildrenClick = useCallback(() => {
    open();
  }, [open]);

  const handleFieldKeyDown = useCallback(
    (event) => {
      if (event.key === 'Enter') {
        event.preventDefault();

        submit();
      }
    },
    [submit],
  );

  const onUserAdd = useCallback(
    (newUserId) => {
      setData((prevData) => ({
        ...prevData,
        userId: newUserId,
      }));
    },
    [setData],
  );

  const onUserRemove = useCallback(() => {
    setData((prevData) => ({
      ...prevData,
      userId: null,
    }));
  }, [setData]);

  const [handleFieldBlur, handleControlMouseOver, handleControlMouseOut] = useClosableForm(
    close,
    isOpened,
  );

  // const handleSubmit = useCallback(() => {
  //   submit();
  // }, [submit]);

  useEffect(() => {
    if (isOpened) {
      nameField.current.ref.current.select();
    }
  }, [isOpened]);

  useDidUpdate(() => {
    nameField.current.ref.current.select();
  }, [selectNameFieldState]);

  if (!isOpened) {
    return React.cloneElement(children, {
      onClick: handleChildrenClick,
    });
  }

  return (
    <Form className={styles.wrapper}>
      <TextArea
        ref={nameField}
        as={TextareaAutosize}
        name="name"
        value={data.name}
        placeholder={t('common.enterTaskDescription')}
        minRows={2}
        spellCheck={false}
        className={classNames(styles.field)}
        onKeyDown={handleFieldKeyDown}
        onChange={handleFieldChange}
        onBlur={handleFieldBlur}
      />
      <div className={styles.controls}>
        {/* eslint-disable-next-line jsx-a11y/mouse-events-have-key-events */}
        <Button
          positive
          content={t('action.addTask')}
          onMouseOver={handleControlMouseOver}
          onMouseOut={handleControlMouseOut}
          onClick={() => submit()}
        />
        {/* eslint-disable-next-line jsx-a11y/mouse-events-have-key-events */}
        <span
          style={{ float: 'right' }}
          onMouseOver={handleControlMouseOver}
          onMouseOut={handleControlMouseOut}
        >
          <DueDateEditPopup defaultValue={data.dueDate} hideInput onUpdate={handleDueDateUpdate}>
            <Button className={classNames(styles.button, styles.taskduedate)}>
              <Icon fitted name="clock outline" className={styles.actionIcon} />
              &nbsp;
              {data.dueDate
                ? t('format:date', {
                    postProcess: 'formatDate',
                    value: data.dueDate,
                  })
                : t('common.dueDate_title')}
            </Button>
          </DueDateEditPopup>
          {userCan.ADD_MEMBER_TO_CARD && (
            <ProjectMembershipsPopup
              items={allProjectMemberships}
              currentUserIds={[data.userId]}
              onUserSelect={onUserAdd}
              onUserDeselect={onUserRemove}
            >
              <Button circular className={classNames(styles.button, styles.taskuser)}>
                {data.userId ? (
                  allProjectMemberships
                    .filter((item) => item.userId === data.userId)
                    .map((item) => (
                      <span key={item.user.id}>
                        <User
                          name={item.user.name}
                          size="small"
                          avatarUrl={item.user.avatarUrl}
                          isDisabled={!userCan.ADD_MEMBER_TO_CARD}
                        />
                      </span>
                    ))
                ) : (
                  <Icon fitted name="add user" size="small" />
                )}
              </Button>
            </ProjectMembershipsPopup>
          )}
        </span>
      </div>
    </Form>
  );
});

Add.propTypes = {
  children: PropTypes.element.isRequired,
  userCan: PropTypes.object.isRequired, // eslint-disable-line react/forbid-prop-types
  allProjectMemberships: PropTypes.array.isRequired, // eslint-disable-line react/forbid-prop-types
  onCreate: PropTypes.func.isRequired,
};

export default React.memo(Add);
