import React, { useCallback, useRef } from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
// import { Progress } from 'semantic-ui-react';

import { Droppable, Draggable, DragDropContext } from 'react-beautiful-dnd';
import classNames from 'classnames';
import { closePopup } from '../../../lib/popup';
import DroppableTypes from '../../../constants/DroppableTypes';
import Item from './Item';
import Add from './Add';
import ProgressBar from '../../ProgressBar';

import styles from './Tasks.module.scss';

const Tasks = React.memo(
  ({ cardId, items, userCan, allProjectMemberships, onCreate, onUpdate, onMove, onDelete }) => {
    const [t] = useTranslation();

    const wrapper = useRef(null);

    const handleUpdate = useCallback(
      (id, data) => {
        onUpdate(id, data);
      },
      [onUpdate],
    );

    const handleDelete = useCallback(
      (id) => {
        onDelete(id);
      },
      [onDelete],
    );

    const handleUserAdd = useCallback(
      (id, data) => {
        onUpdate(id, { userId: data });
      },
      [onUpdate],
    );

    const handleUserRemove = useCallback(
      (id, data) => {
        onUpdate(id, { userId: null, removedUserId: data });
      },
      [onUpdate],
    );

    const handleDragStart = useCallback(() => {
      closePopup();
    }, []);

    const handleDragEnd = useCallback(
      ({ draggableId, type, source, destination }) => {
        if (
          !destination ||
          (source.droppableId === destination.droppableId && source.index === destination.index)
        ) {
          return;
        }

        switch (type) {
          case DroppableTypes.TASK:
            onMove(draggableId, destination.index);

            break;
          default:
        }
      },
      [onMove],
    );

    const getRenderItem = (item, taskIndex) => (provided, snapshot) => (
      <div
        // eslint-disable-next-line react/jsx-props-no-spreading
        {...provided.draggableProps}
        // eslint-disable-next-line react/jsx-props-no-spreading
        {...provided.dragHandleProps}
        ref={provided.innerRef}
        data-drag-scroller
        className={classNames(snapshot.isDragging && styles.drag)}
      >
        <Item
          key={item.id}
          id={item.id}
          index={taskIndex}
          name={item.name}
          approve={item.approve}
          userId={item.userId}
          userCan={userCan}
          allProjectMemberships={allProjectMemberships}
          isCompleted={item.isCompleted}
          isPersisted={item.isPersisted}
          dueDate={item.dueDate && new Date(item.dueDate)}
          onUpdate={(data) => handleUpdate(item.id, data)}
          onDelete={() => handleDelete(item.id)}
          onUserAdd={(data) => handleUserAdd(item.id, data)}
          onUserRemove={() => handleUserRemove(item.id)}
        />
      </div>
    );

    const completedItems = items.filter((item) => item.isCompleted);
    const reviewItems = items.filter((item) => item.approve === 'approveReview');
    const redoItems = items.filter((item) => item.approve === 'approveRedo');

    const progresses = [
      {
        name: t('action.approveDone_title'),
        value: completedItems.length,
        color: '#2185d0',
      },
      {
        name: t('action.approveReview_title'),
        value: reviewItems.length,
        color: '#fbbd08',
      },
      {
        name: t('action.approveRedo_title'),
        value: redoItems.length,
        color: '#db2828',
      },
    ];

    return (
      <>
        {/* eslint-disable-next-line jsx-a11y/no-static-element-interactions */}
        <div ref={wrapper} className={styles.tasksWrapper}>
          {items.length > 0 && (
            <ProgressBar
              showLegend
              items={progresses}
              total={items.length}
              isCompleted={completedItems.length === items.length}
            />
          )}
          <div className={styles.tasks}>
            <DragDropContext onDragStart={handleDragStart} onDragEnd={handleDragEnd}>
              <Droppable droppableId={`cardTasks:${cardId}`} type={DroppableTypes.TASK}>
                {({ innerRef, droppableProps, placeholder }) => (
                  <div
                    // eslint-disable-next-line react/jsx-props-no-spreading
                    {...droppableProps}
                    ref={innerRef}
                    data-drag-scroller
                  >
                    {items.map((item, index) => (
                      <Draggable key={item.id} draggableId={item.id} index={index}>
                        {getRenderItem(item, index)}
                      </Draggable>
                    ))}
                    {placeholder}
                  </div>
                )}
              </Droppable>
            </DragDropContext>
          </div>
          <Add onCreate={onCreate} userCan={userCan} allProjectMemberships={allProjectMemberships}>
            <button type="button" className={styles.taskButton}>
              <span className={styles.taskButtonText}>
                {items.length > 0 ? t('action.addAnotherTask') : t('action.addTask')}
              </span>
            </button>
          </Add>
        </div>
      </>
    );
  },
);

Tasks.propTypes = {
  cardId: PropTypes.string.isRequired,
  items: PropTypes.array.isRequired, // eslint-disable-line react/forbid-prop-types
  userCan: PropTypes.object.isRequired, // eslint-disable-line react/forbid-prop-types
  allProjectMemberships: PropTypes.array.isRequired, // eslint-disable-line react/forbid-prop-types
  onCreate: PropTypes.func.isRequired,
  onUpdate: PropTypes.func.isRequired,
  onMove: PropTypes.func.isRequired,
  onDelete: PropTypes.func.isRequired,
};

export default Tasks;
