/* eslint-disable import/prefer-default-export */
import { call, put } from 'redux-saga/effects';

import request from './request';
import {
  fetchBoardChartFailed,
  fetchBoardChartRequested,
  fetchBoardChartSucceeded,
} from '../../../actions';
import api from '../../../api';

export function* fetchBoardChartRequest(id) {
  yield put(fetchBoardChartRequested(id));

  try {
    const {
      item,
      included: { labels, lists, cards, cardMemberships, cardLabels, tasks, attachments },
    } = yield call(request, api.getBoardChart, id);

    const action = fetchBoardChartSucceeded(
      item,
      labels,
      lists,
      cards,
      cardMemberships,
      cardLabels,
      tasks,
      attachments,
    );
    yield put(action);

    return {
      success: true,
      payload: action.payload,
    };
  } catch (error) {
    const action = fetchBoardChartFailed(id, error);
    yield put(action);

    return {
      success: false,
      payload: action.payload,
    };
  }
}
