import ActionTypes from '../constants/ActionTypes';

const initialState = {
  text: '',
  projects: [],
  boards: [],
  cards: [],
  tasks: [],
  labels: [],
  isSubmitting: false,
};

export default (state = initialState, { type, payload }) => {
  switch (type) {
    case ActionTypes.SEARCH_FETCH_REQUESTED:
      return {
        ...initialState,
        text: payload.text,
        isSubmitting: true,
      };
    case ActionTypes.SEARCH_FETCH_SUCCEEDED:
      return {
        ...state,
        ...payload,
        text: state.text,
        isSubmitting: false,
      };
    case ActionTypes.SEARCH_FETCH_FAILED:
      return {
        ...state,
        isSubmitting: false,
      };
    default:
      return state;
  }
};
