/* eslint-disable react/forbid-prop-types */
/* eslint-disable no-shadow */
/* eslint-disable camelcase */
import React from 'react';
import PropTypes from 'prop-types';
import TimeLine from './lib/TimeLine';

const GanttChart = React.memo(({ name, listBoards, listList, listCard, listLabels, listUsers }) => {
  const links = {};
  const result_data = [];
  listBoards.forEach((boardItem) => {
    const { id, name } = boardItem;
    const record_board = {
      id,
      name,
      start: '',
      end: '',
      description: '',
      labels: '',
      member: '',
      type: 'board',
      color: '#2779bd',
    };
    result_data.push(record_board);
    listList.forEach((listItem) => {
      if (listItem[1].boardId === id) {
        const { id, name } = listItem[1];
        const listID = listItem[1].id;
        const record = {
          id,
          name,
          start: '',
          end: '',
          description: '',
          labels: '',
          member: '',
          type: 'tab',
          color: '#2779bd',
        };
        result_data.push(record);
        listCard.forEach((cardItem) => {
          const labelsValue = [];
          listLabels.forEach((labelItem) => {
            const labelItemValue = Object.entries(labelItem);
            if (labelItemValue[0][0] === cardItem[1].id) {
              labelsValue.push([labelItemValue[0][1].name, labelItemValue[0][1].color]);
            }
          });
          const usersValue = [];
          listUsers.forEach((userItem) => {
            const userItemValue = Object.entries(userItem);
            if (userItemValue[0][0] === cardItem[1].id) {
              usersValue.push([userItemValue[0][1].name, userItemValue[0][1].avatarUrl]);
            }
          });
          if (cardItem[1].listId === listID) {
            const { id, name, createdAt, dueDate, description } = cardItem[1];
            const starDate_card = new Date(createdAt);
            let dueDate_card = '';
            if (dueDate != null && dueDate !== '') {
              dueDate_card = new Date(dueDate);
            } else {
              dueDate_card = new Date(createdAt);
            }
            const record_card = {
              id,
              name,
              start: starDate_card,
              end: dueDate_card,
              description,
              labels: labelsValue,
              member: usersValue,
              type: 'card',
              color: '#2779bd',
            };
            result_data.push(record_card);
          }
        });
      }
    });
  });

  return (
    <div className="time-line-container">
      <TimeLine data={result_data} links={links} project={name} />
    </div>
  );
});

GanttChart.propTypes = {
  name: PropTypes.string.isRequired,
  listBoards: PropTypes.array.isRequired,
  listList: PropTypes.array.isRequired,
  listCard: PropTypes.array.isRequired,
  listLabels: PropTypes.array.isRequired,
  listUsers: PropTypes.array.isRequired,
};

export default GanttChart;
