import React from 'react';
import { useTranslation } from 'react-i18next';

const HeaderTaskList = React.memo(() => {
  const [t] = useTranslation();

  return (
    <div className="timeLine-sidebar-wrapper">
      <div className="timeLine-cart">
        <div className="timeLine-header-title">{t('chart.card')}</div>
      </div>
      <div className="timeLine-label">
        <div className="timeLine-header-title">{t('chart.label')}</div>
      </div>
      <div className="timeLine-member">
        <div className="timeLine-header-title">{t('chart.member')}</div>
      </div>
      <div className="timeLine-start">
        <div className="timeLine-header-title">{t('chart.start')}</div>
      </div>
      <div className="timeLine-end">
        <div className="timeLine-header-title">{t('chart.end')}</div>
      </div>
    </div>
  );
});

export default HeaderTaskList;
