import upperFirst from 'lodash/upperFirst';
import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

import styles from './ProgressBar.module.scss';

const SIZES = {
  TINY: 'tiny',
  SMALL: 'small',
  MEDIUM: 'medium',
};

// const COLORS = [
//   'emerald',
//   'peter-river',
//   'wisteria',
//   'carrot',
//   'alizarin',
//   'turquoise',
//   'midnight-blue',
// ];

const ProgressBar = React.memo(({ items, total, showLegend, isCompleted, size }) => {
  const bars =
    items &&
    items.length &&
    items.map(
      (item, index) =>
        item.value > 0 && (
          <div
            className={classNames(styles.bars, styles.bar)}
            style={{
              backgroundColor: isCompleted ? '#61bd4f' : item.color,
              width: `${(item.value / total) * 100}%`,
            }}
            // eslint-disable-next-line react/no-array-index-key
            key={index}
          />
        ),
      [items],
    );

  const legends =
    showLegend &&
    items &&
    items.length &&
    items.map(
      (item, index) =>
        item.value > 0 && (
          // eslint-disable-next-line react/no-array-index-key
          <div className={classNames(styles.legends, styles.legend)} key={index}>
            <span className={styles.dot} style={{ color: isCompleted ? '#61bd4f' : item.color }}>
              ●
            </span>
            <span className={styles.label}>{`${((item.value / total) * 100).toFixed(2)}% ${
              item.name
            }`}</span>
          </div>
        ),
      [items],
    );

  return (
    <div className={classNames(styles.wrapper)}>
      <div className={classNames(styles.bars, styles[`wrapper${upperFirst(size)}`])}>
        {bars === '' ? '' : bars}
      </div>
      <div className={classNames(styles.legends)}>{legends === '' ? '' : legends}</div>
    </div>
  );
});

ProgressBar.propTypes = {
  // eslint-disable-next-line react/forbid-prop-types
  items: PropTypes.array.isRequired,
  total: PropTypes.number.isRequired,
  showLegend: PropTypes.bool,
  isCompleted: PropTypes.bool,
  size: PropTypes.oneOf(Object.values(SIZES)),
};

ProgressBar.defaultProps = {
  showLegend: false,
  isCompleted: false,
  size: SIZES.TINY,
};

export default ProgressBar;
