import React, { useCallback, useState } from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import { Button, Comment, Icon, Loader, Visibility } from 'semantic-ui-react';

import { ActionTypes } from '../../../constants/Enums';
import CommentAdd from './CommentAdd';
import Item from './Item';

import styles from './Actions.module.scss';

const Actions = React.memo(
  ({
    items,
    isFetching,
    isAllFetched,
    userCan,
    currentUser,
    onFetch,
    onCommentCreate,
    onCommentUpdate,
    onCommentDelete,
  }) => {
    const [t] = useTranslation();

    const SHOW_DETAILS_KEY = 'showDetails';
    const [showDetails, setShowDetails] = useState(
      localStorage.getItem(SHOW_DETAILS_KEY) === 'true',
    );

    const handleToggleDetails = useCallback(() => {
      localStorage.setItem(SHOW_DETAILS_KEY, !showDetails);
      setShowDetails(!showDetails);

      if (!showDetails) onFetch();
      else onFetch(ActionTypes.COMMENT_CARD);
    }, [showDetails, onFetch]);

    const handleOnScreen = useCallback(() => {
      // if (showDetails) onFetch();
      // else onFetch(ActionTypes.COMMENT_CARD);
      onFetch();
    }, [onFetch]);

    const handleCommentUpdate = useCallback(
      (id, data) => {
        onCommentUpdate(id, data);
      },
      [onCommentUpdate],
    );

    const handleCommentDelete = useCallback(
      (id) => {
        onCommentDelete(id);
      },
      [onCommentDelete],
    );

    return (
      <>
        <div className={styles.contentModule}>
          <div className={styles.moduleWrapper}>
            <Button floated="right" size="mini" onClick={handleToggleDetails}>
              {showDetails ? t('action.hideDetails') : t('action.showDetails')}
            </Button>
            <Icon name="list ul" className={styles.moduleIcon} />
            <div className={styles.moduleHeader}>{t('common.actions')}</div>
            <div className={styles.wrapper}>
              <Comment.Group>
                <CommentAdd onCreate={onCommentCreate} currentUser={currentUser} />
                {items.map((item) =>
                  item.type === ActionTypes.COMMENT_CARD ? (
                    <Item.Comment
                      key={item.id}
                      data={item.data}
                      createdAt={item.createdAt}
                      isPersisted={item.isPersisted}
                      user={item.user}
                      userCan={userCan}
                      onUpdate={(data) => handleCommentUpdate(item.id, data)}
                      onDelete={() => handleCommentDelete(item.id)}
                    />
                  ) : (
                    showDetails && (
                      <Item
                        key={item.id}
                        type={item.type}
                        data={item.data}
                        createdAt={item.createdAt}
                        user={item.user}
                      />
                    )
                  ),
                )}
              </Comment.Group>
            </div>
            {isFetching ? (
              <Loader active inverted inline="centered" size="small" className={styles.loader} />
            ) : (
              !isAllFetched && <Visibility fireOnMount onOnScreen={handleOnScreen} />
            )}
          </div>
        </div>
      </>
    );
  },
);

Actions.propTypes = {
  items: PropTypes.array.isRequired, // eslint-disable-line react/forbid-prop-types
  isFetching: PropTypes.bool.isRequired,
  isAllFetched: PropTypes.bool.isRequired,
  userCan: PropTypes.object.isRequired, // eslint-disable-line react/forbid-prop-types
  currentUser: PropTypes.object.isRequired, // eslint-disable-line react/forbid-prop-types
  onFetch: PropTypes.func.isRequired,
  onCommentCreate: PropTypes.func.isRequired,
  onCommentUpdate: PropTypes.func.isRequired,
  onCommentDelete: PropTypes.func.isRequired,
};

export default Actions;
