import { connect } from 'react-redux';

import { boardsForCurrentProjectSelector, currentProjectSelector } from '../selectors';
import GanttChart from '../components/GanttChart';

const mapStateToProps = (state) => {
  const { name } = currentProjectSelector(state);
  const listBoards = boardsForCurrentProjectSelector(state);
  const listList = Object.entries(state.orm.List.itemsById);
  const listCard = Object.entries(state.orm.Card.itemsById);
  const listLabel = Object.entries(state.orm.Label.itemsById);
  listLabel.forEach((labelItem) => {
    listLabel[labelItem[1].id] = labelItem;
  });
  const cardLabels = Object.entries(state.orm.CardLabels.itemsById);
  const listLabels = [];
  cardLabels.forEach((cardLabel) => {
    listLabels.push({ [cardLabel[1].fromCardId]: listLabel[cardLabel[1].toLabelId][1] });
  });

  const listUser = Object.entries(state.orm.User.itemsById);
  listUser.forEach((userItem) => {
    listUser[userItem[1].id] = userItem;
  });

  const cardUsers = Object.entries(state.orm.CardUsers.itemsById);
  const listUsers = [];
  cardUsers.forEach((cardUser) => {
    listUsers.push({ [cardUser[1].fromCardId]: listUser[cardUser[1].toUserId][1] });
  });

  return {
    name,
    listBoards,
    listList,
    listCard,
    listLabels,
    listUsers,
  };
};

export default connect(mapStateToProps)(GanttChart);
