import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import { Button, Dropdown, Table } from 'semantic-ui-react';

import DeletePopup from '../DeletePopup';
import Roles from '../../constants/Roles';

import styles from './Item.module.scss';

const Item = React.memo(({ name, username, email, role, userCan, onUpdate, onDelete }) => {
  const [t] = useTranslation();

  const handleRoleChange = useCallback(
    (e, { value }) => {
      onUpdate({
        role: value,
      });
    },
    [onUpdate],
  );

  return (
    <Table.Row>
      <Table.Cell>{name}</Table.Cell>
      <Table.Cell>{username || '-'}</Table.Cell>
      <Table.Cell>{email}</Table.Cell>
      <Table.Cell collapsing>
        <Dropdown
          fluid
          selection
          name="newRole"
          options={Roles.map((key) => ({
            text: t(`role.${key}`),
            value: key,
          }))}
          value={role}
          placeholder={t('role.role')}
          className={styles.field}
          disabled={!userCan.UPDATE_USER_ROLE}
          onChange={handleRoleChange}
        />
      </Table.Cell>
      <Table.Cell collapsing>
        {userCan.DELETE_USER && (
          <DeletePopup
            title={t('common.deleteUser', {
              context: 'title',
            })}
            content={t('common.areYouSureYouWantToDeleteThisUser')}
            buttonContent={t('action.deleteUser')}
            onConfirm={onDelete}
          >
            <Button content={t('action.delete')} className={styles.button} />
          </DeletePopup>
        )}
      </Table.Cell>
    </Table.Row>
  );
});

Item.propTypes = {
  name: PropTypes.string.isRequired,
  username: PropTypes.string,
  email: PropTypes.string.isRequired,
  role: PropTypes.string.isRequired,
  // eslint-disable-next-line react/forbid-prop-types
  userCan: PropTypes.object.isRequired,
  onUpdate: PropTypes.func.isRequired,
  onDelete: PropTypes.func.isRequired,
};

Item.defaultProps = {
  username: undefined,
};

export default Item;
