/* eslint-disable class-methods-use-this */
/* eslint-disable no-shadow */
/* eslint-disable no-param-reassign */
/* eslint-disable no-restricted-syntax */
const defvalues = {
  header: {
    top: {
      style: {
        backgroundColor: '#333333',
        fontSize: 10,
        color: '#ffffff',
        textAlign: 'center',
      },
    },
    middle: {
      style: {
        backgroundColor: '#D2691E',
      },
      selectedStyle: {
        backgroundColor: '#b13525',
        fontWeight: 'bold',
      },
    },
    bottom: {
      style: {
        background: '#808080',
        color: '#ffffff',
        fontSize: 9,
      },
      selectedStyle: {
        backgroundColor: '#b13525',
        fontWeight: 'bold',
      },
    },
  },
  taskList: {
    title: {
      label: 'Projects',
      style: {
        backgroundColor: '#333333',
        borderBottom: 'solid 1px #C0C0C0',
        color: '#ffffff',
        textAlign: 'center',
      },
    },
    task: {
      style: {
        backgroundColor: '#fbf9f9',
      },
    },
    verticalSeparator: {
      style: {
        backgroundColor: '#333333',
      },
      grip: {
        style: {
          backgroundColor: '#cfcfcd',
        },
      },
    },
  },
  dataViewPort: {
    rows: {
      style: {
        backgroundColor: '#fbf9f9',
        borderBottom: 'solid 0.5px #cfcfcd',
      },
    },
    task: {
      showLabel: false,

      style: {
        position: 'absolute',
        borderRadius: 3,
        color: '#ffffff',
        textAlign: 'center',
        backgroundColor: '#fbf9f9',
      },
      selectedStyle: {
        position: 'absolute',
        borderRadius: 3,
        border: 'solid 1px #ff00fa',
        color: '#ffffff',
        textAlign: 'center',
        backgroundColor: '#fbf9f9',
      },
    },
  },
  links: {
    color: '#000000',
    selectedColor: '#ff00fa',
  },
};

class Config {
  constructor() {
    this.data = defvalues;
  }

  load = (values) => {
    this.data = {};
    if (values) this.populate(values, defvalues, this.data);
    else this.data = defvalues;
  };

  populate(values, defvalues, final) {
    if (!this.isObject(defvalues)) return;
    for (const key in defvalues) {
      if (!values[key]) {
        // if not exits
        final[key] = defvalues[key];
      } else {
        // if it does
        final[key] = values[key];
        this.populate(values[key], defvalues[key], final[key]);
      }
    }
  }

  isObject(value) {
    if (typeof value === 'string' || typeof value === 'boolean' || typeof value === 'number')
      return false;
    return true;
  }

  get values() {
    return this.data;
  }
}

const config = new Config();
export default config;
