import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { currentUserSelector, allUsersExceptCurrentSelector } from '../selectors';
import { closeModal, deleteUser, updateUser } from '../actions/entry';
import UsersModal from '../components/UsersModal';
import Can from '../constants/Can';

const mapStateToProps = (state) => {
  const items = allUsersExceptCurrentSelector(state);
  const currentUser = currentUserSelector(state);

  return {
    items,
    userCan: Can[currentUser.role],
  };
};

const mapDispatchToProps = (dispatch) =>
  bindActionCreators(
    {
      onUpdate: updateUser,
      onDelete: deleteUser,
      onClose: closeModal,
    },
    dispatch,
  );

export default connect(mapStateToProps, mapDispatchToProps)(UsersModal);
