import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { Label } from 'semantic-ui-react';
import { useToggle } from '../../lib/hooks';
import DueDate from '../DueDate';
import ProgressBar from '../ProgressBar';
import styles from './Tasks.module.scss';

const Tasks = React.memo(({ items }) => {
  const [isOpened, toggleOpened] = useToggle();

  const handleToggleClick = useCallback(
    (event) => {
      event.preventDefault();

      toggleOpened();
    },
    [toggleOpened],
  );

  const completedItems = items.filter((item) => item.isCompleted);
  const reviewItems = items.filter((item) => item.approve === 'approveReview');
  const redoItems = items.filter((item) => item.approve === 'approveRedo');

  const progresses = [
    {
      name: 'Complete',
      value: completedItems.length,
      color: '#2185d0',
    },
    {
      name: 'Review',
      value: reviewItems.length,
      color: '#fbbd08',
    },
    {
      name: 'Redo',
      value: redoItems.length,
      color: '#db2828',
    },
  ];
  const statusColors = { approveReview: 'yellow', approveRedo: 'red' };

  return (
    <>
      {/* eslint-disable jsx-a11y/click-events-have-key-events,
                         jsx-a11y/no-static-element-interactions */}
      <div className={styles.button} onClick={handleToggleClick}>
        {/* eslint-enable jsx-a11y/click-events-have-key-events,
                          jsx-a11y/no-static-element-interactions */}
        <span className={styles.progressWrapper}>
          <ProgressBar
            items={progresses}
            total={items.length}
            isCompleted={completedItems.length === items.length}
          />
        </span>
        <span
          className={classNames(styles.count, isOpened ? styles.countOpened : styles.countClosed)}
        >
          {completedItems.length}
          {'/'}
          {items.length}
        </span>
      </div>
      {isOpened && (
        <ul className={styles.tasks}>
          {items.map((item) => (
            <li
              key={item.id}
              className={classNames(styles.task, item.isCompleted && styles.taskCompleted)}
            >
              {item.dueDate && (
                <span
                  className={classNames(
                    styles.attachment,
                    styles.attachmentLeft,
                    styles.taskduedate,
                  )}
                >
                  <DueDate value={new Date(item.dueDate)} size="tiny" />
                </span>
              )}
              <Label circular color={statusColors[item.approve]} size="mini" horizontal empty />
              {item.name}
            </li>
          ))}
        </ul>
      )}
    </>
  );
});

Tasks.propTypes = {
  items: PropTypes.array.isRequired, // eslint-disable-line react/forbid-prop-types
};

export default Tasks;
