import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import i18n from 'i18next';
import { useTranslation, Trans } from 'react-i18next';
import { Popup, Comment } from 'semantic-ui-react';

import { ActionTypes } from '../../../constants/Enums';
import ItemComment from './ItemComment';
import User from '../../User';

import styles from './Item.module.scss';

const Item = React.memo(({ type, data, createdAt, user }) => {
  const [t] = useTranslation();

  let contentNode;

  switch (type) {
    case ActionTypes.CREATE_CARD:
      contentNode = (
        <Trans
          i18nKey="common.userAddedThisCardToList"
          values={{
            user: user.name,
            list: data.list.name,
          }}
        >
          <span className={styles.author}>{user.name}</span>
          <span className={styles.text}>
            {' added this card to '}
            {data.list.name}
          </span>
        </Trans>
      );

      break;
    case ActionTypes.MOVE_CARD:
      contentNode = (
        <Trans
          i18nKey="common.userMovedThisCardFromListToList"
          values={{
            user: user.name,
            fromList: data.fromList.name,
            toList: data.toList.name,
          }}
        >
          <span className={styles.author}>{user.name}</span>
          <span className={styles.text}>
            {' moved this card from '}
            {data.fromList.name}
            {' to '}
            {data.toList.name}
          </span>
        </Trans>
      );

      break;
    case ActionTypes.ASSIGN_CARD:
      contentNode = (
        <Trans
          i18nKey="common.userAssignedMemberToCard"
          values={{
            user: user.name,
            member: data.user.name,
            card: 'this card',
          }}
        >
          <span className={styles.author}>{user.name}</span>
          {' assigned '}
          <strong>{data.user.name}</strong>
          {' to this card '}
        </Trans>
      );

      break;
    case ActionTypes.UNASSIGN_CARD:
      contentNode = (
        <Trans
          i18nKey="common.userUnassignedMemberFromCard"
          values={{
            user: user.name,
            member: data.user.name,
            card: 'this card',
          }}
        >
          <span className={styles.author}>{user.name}</span>
          {' removed '}
          <strong>{data.user.name}</strong>
          {' from '}
          {' this card '}
        </Trans>
      );

      break;
    case ActionTypes.APPROVE_TASK:
      contentNode = (
        <Trans
          i18nKey="common.userChangeApproveTaskThisCard"
          values={{
            user: user.name,
            task: data.task.name,
            status: t(`action.${data.task.status}_title`),
          }}
        >
          <span className={styles.author}>{user.name}</span>
          {' changed task '}
          <Popup trigger={<span className={styles.tasktitle}>{data.task.name}</span>} />
          {' to '}
          <strong>{t(`action.${data.task.status}_title`)}</strong>
        </Trans>
      );

      break;
    default:
      contentNode = null;
  }

  return (
    <Comment>
      <span className={styles.user}>
        <User name={user.name} avatarUrl={user.avatarUrl} />
      </span>
      <div className={classNames(styles.content)}>
        <div>{contentNode}</div>
        <span className={styles.date}>
          {/* {t('format:longDateTime', {
            postProcess: 'formatRelative',
            value: createdAt,
          })} */}
          {i18n.dateFns.formatRelative(createdAt, new Date())}
        </span>
      </div>
    </Comment>
  );
});

Item.Comment = ItemComment;

Item.propTypes = {
  type: PropTypes.string.isRequired,
  data: PropTypes.object.isRequired, // eslint-disable-line react/forbid-prop-types
  createdAt: PropTypes.instanceOf(Date).isRequired,
  user: PropTypes.object.isRequired, // eslint-disable-line react/forbid-prop-types
};

export default Item;
