import ActionTypes from '../constants/ActionTypes';

export const fetchBoardChartRequested = (id) => ({
  type: ActionTypes.BOARD_FETCH_REQUESTED,
  payload: {
    id,
  },
});

export const fetchBoardChartSucceeded = (
  board,
  labels,
  lists,
  cards,
  cardMemberships,
  cardLabels,
  tasks,
  attachments,
) => ({
  type: ActionTypes.BOARD_FETCH_SUCCEEDED,
  payload: {
    board,
    labels,
    lists,
    cards,
    cardMemberships,
    cardLabels,
    tasks,
    attachments,
  },
});

export const fetchBoardChartFailed = (id, error) => ({
  type: ActionTypes.BOARD_FETCH_FAILED,
  payload: {
    id,
    error,
  },
});
