import { all, takeLatest } from 'redux-saga/effects';

import { fetchBoardChart } from '../services';
import EntryActionTypes from '../../../constants/EntryActionTypes';

export default function* boardChartWatchers() {
  yield all([
    takeLatest(EntryActionTypes.BOARD_FETCH, ({ payload: { id } }) => fetchBoardChart(id)),
  ]);
}
