import React from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import './Footer.css';

const Footer = React.memo(({ project }) => {
  const [t] = useTranslation();

  return (
    <div id="containerLegenda">
      <div id="divProvideWidthForLegenda">
        <table id="gridLegenda">
          <tbody>
            <tr>
              <td id="containerMetaData">
                <table className="metadata" id="metatdatatable">
                  <tbody>
                    <tr>
                      <th>
                        <div>{t('chart.project')}:</div>
                      </th>
                      <td>
                        <div>{project}</div>
                      </td>
                    </tr>
                    <tr>
                      <th>
                        <div>{t('chart.lastUpdate')}:</div>
                      </th>
                      <td>
                        <div />
                      </td>
                    </tr>
                  </tbody>
                </table>
              </td>
              <td className="containerLegendaCol">
                <div className="row">
                  <div className="legenddragarrow" />
                  <div className="period period_23" />
                  <div className="period-label">{t('chart.timeBlockPending')}</div>
                </div>
              </td>
              <td className="containerLegendaCol">
                <div className="row">
                  <div className="legenddragarrow" />
                  <div className="period period_15" />
                  <div className="period-label">{t('chart.timeBlockDone')}</div>
                </div>
              </td>
              <td className="containerLegendaCol">
                <div className="legenddragarrow" />
                <div className="symbol symbol_28" />
                <div>{t('chart.dueDatePending')}</div>
                <div className="row">
                  <div className="legenddragarrow" />
                  <div className="symbol symbol_27" />
                  <div>{t('chart.dueDateDone')}</div>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
        <div id="dummyLegenda">
          <div id="legendaperiods" />
          <div id="legendasymbols" />
        </div>
      </div>
    </div>
  );
});

Footer.propTypes = {
  project: PropTypes.string.isRequired,
};

export default Footer;
