import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import { Menu } from 'semantic-ui-react';
import { useHistory } from 'react-router-dom';
import Paths from '../../constants/Paths';
import { withPopup } from '../../lib/popup';
import { Popup } from '../../lib/custom-ui';
import styles from './ActionsPopup.module.scss';

const ActionsStep = React.memo(({ onCopy, projectId, projectName }) => {
  const [t] = useTranslation();

  const history = useHistory();

  const handleCopyClick = useCallback(() => {
    onCopy(projectId, `${t('action.copyOfProject')} ${projectName}`);
  }, [onCopy, projectId, projectName, t]);

  const handleChartClick = useCallback(() => {
    history.push(Paths.PROJECTSCHART.replace(':id', projectId));
  }, [history, projectId]);

  return (
    <>
      <Popup.Header>
        {t('common.projectActions', {
          context: 'title',
        })}
      </Popup.Header>
      <Popup.Content>
        <Menu secondary vertical className={styles.menu}>
          <Menu.Item className={styles.menuItem} onClick={handleCopyClick}>
            {t('action.copyProject', {
              context: 'title',
            })}
          </Menu.Item>
          <Menu.Item className={styles.menuItem} onClick={handleChartClick}>
            {t('action.projectChart', {
              context: 'title',
            })}
          </Menu.Item>
        </Menu>
      </Popup.Content>
    </>
  );
});

ActionsStep.propTypes = {
  onCopy: PropTypes.func.isRequired,
  projectId: PropTypes.string.isRequired,
  projectName: PropTypes.string.isRequired,
};

export default withPopup(ActionsStep);
