import React, { useCallback, useRef } from 'react';
import { useTranslation } from 'react-i18next';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { Button, Checkbox, Icon, Label } from 'semantic-ui-react';

import NameEdit from './NameEdit';
import ActionsPopup from './ActionsPopup';
import DueDateEditPopup from '../../DueDateEditPopup';
import User from '../../User';
import ProjectMembershipsPopup from '../../ProjectMembershipsPopup';
import DueDate from '../../DueDate';
import { closePopup } from '../../../lib/popup';

import styles from './Item.module.scss';

const Item = React.memo(
  ({
    name,
    approve,
    dueDate,
    userId,
    userCan,
    isCompleted,
    isPersisted,
    allProjectMemberships,
    onUpdate,
    onDelete,
    onUserAdd,
    onUserRemove,
  }) => {
    const [t] = useTranslation();
    const nameEdit = useRef(null);

    const handleClick = useCallback(() => {
      if (userCan.CREATE_TASK && isPersisted) {
        nameEdit.current.open();
      }
    }, [userCan, isPersisted]);

    const handleNameUpdate = useCallback(
      (newName) => {
        if (userCan.CREATE_TASK) {
          onUpdate({
            name: newName,
          });
        }
      },
      [userCan, onUpdate],
    );

    const handleToggleChange = useCallback(() => {
      if (userCan.CREATE_TASK) {
        onUpdate({
          isCompleted: !isCompleted,
          approve: isCompleted ? 'approveTodo' : 'approveDone',
        });
      } else if (!isCompleted) {
        onUpdate({
          approve: approve === 'approveReview' ? 'approveTodo' : 'approveReview',
        });
      }
    }, [userCan, isCompleted, onUpdate, approve]);

    const handleNameEdit = useCallback(() => {
      if (userCan.CREATE_TASK) nameEdit.current.open();
    }, [userCan]);
    const handleApprove = useCallback(
      (title) => {
        onUpdate({
          approve: isCompleted || approve === title ? 'approveTodo' : title,
        });
        closePopup();
      },
      [approve, isCompleted, onUpdate],
    );
    const handleDueDateUpdate = useCallback(
      (newDueDate) => {
        if (userCan.CREATE_TASK) {
          onUpdate({
            dueDate: newDueDate,
          });
        }
      },
      [userCan, onUpdate],
    );
    const statusColors = { approveReview: 'yellow', approveRedo: 'red' };
    const users = allProjectMemberships.filter((item) => item.userId === userId);
    const fixtarget = dueDate || userId || approve === 'approveReview' || approve === 'approveRedo';

    return (
      <div className={styles.wrapper}>
        <span className={styles.checkboxWrapper}>
          <Checkbox
            checked={isCompleted}
            indeterminate={approve === 'approveReview'}
            disabled={!isPersisted}
            className={styles.checkbox}
            onChange={handleToggleChange}
          />
        </span>
        <NameEdit ref={nameEdit} defaultValue={name} onUpdate={handleNameUpdate}>
          <div className={styles.content}>
            {/* eslint-disable jsx-a11y/click-events-have-key-events,
                              jsx-a11y/no-static-element-interactions */}
            <div className={styles.text} onClick={handleClick}>
              <div className={classNames(styles.actionButton)}>
                {statusColors[approve] && (
                  <div className={classNames(styles.attachment, styles.attachmentLeft)}>
                    <ActionsPopup
                      onNameEdit={handleNameEdit}
                      onDelete={onDelete}
                      onApprove={handleApprove}
                      approve={approve}
                      userCan={userCan}
                      isApproveMenu
                    >
                      <Label
                        circular
                        color={statusColors[approve]}
                        size="small"
                        className={classNames(styles.approve)}
                        onClick={null}
                      >
                        {t(`action.${approve}`, {
                          context: 'title',
                        })}
                      </Label>
                    </ActionsPopup>
                  </div>
                )}
                {dueDate ? (
                  <div className={classNames(styles.attachment, styles.attachmentLeft)}>
                    {userCan.CREATE_TASK ? (
                      <DueDateEditPopup
                        hideInput
                        defaultValue={dueDate}
                        onUpdate={handleDueDateUpdate}
                      >
                        <DueDate
                          value={dueDate}
                          size="small"
                          isCompleted={isCompleted}
                          className={classNames(
                            styles.button,
                            styles.taskduedate,
                            styles.target,
                            fixtarget && styles.fixtarget,
                          )}
                        />
                      </DueDateEditPopup>
                    ) : (
                      <DueDate
                        value={dueDate}
                        size="small"
                        isCompleted={isCompleted}
                        className={classNames(
                          styles.button,
                          styles.taskduedate,
                          styles.target,
                          fixtarget && styles.fixtarget,
                        )}
                      />
                    )}
                  </div>
                ) : (
                  userCan.CREATE_TASK && (
                    <DueDateEditPopup
                      hideInput
                      defaultValue={dueDate}
                      onUpdate={handleDueDateUpdate}
                    >
                      <Button
                        className={classNames(
                          styles.button,
                          styles.taskduedate,
                          styles.target,
                          fixtarget && styles.fixtarget,
                        )}
                      >
                        <Icon fitted name="clock outline" className={styles.actionIcon} />
                      </Button>
                    </DueDateEditPopup>
                  )
                )}
                {users.length > 0 &&
                  users.map((item) => (
                    <span
                      key={item.user.id}
                      className={classNames(styles.attachment, styles.attachmentLeft)}
                    >
                      <ProjectMembershipsPopup
                        items={allProjectMemberships}
                        currentUserIds={[userId]}
                        onUserSelect={onUserAdd}
                        onUserDeselect={onUserRemove}
                      >
                        <User
                          name={item.user.name}
                          size="tiny"
                          avatarUrl={item.user.avatarUrl}
                          isDisabled={!userCan.ADD_MEMBER_TO_CARD}
                        />
                      </ProjectMembershipsPopup>
                    </span>
                  ))}
                {!userId && userCan.ADD_MEMBER_TO_CARD && (
                  <ProjectMembershipsPopup
                    items={allProjectMemberships}
                    currentUserIds={[userId]}
                    onUserSelect={onUserAdd}
                    onUserDeselect={onUserRemove}
                  >
                    <Button
                      circular
                      className={classNames(
                        styles.button,
                        styles.taskuser,
                        styles.target,
                        fixtarget && styles.fixtarget,
                      )}
                    >
                      <Icon fitted name="add user" size="small" />
                    </Button>
                  </ProjectMembershipsPopup>
                )}
                {isPersisted && (
                  <ActionsPopup
                    onNameEdit={handleNameEdit}
                    onDelete={onDelete}
                    onApprove={handleApprove}
                    onDueDateUpdate={handleDueDateUpdate}
                    approve={approve}
                    userCan={userCan}
                  >
                    <Button className={classNames(styles.button, styles.target)}>
                      <Icon fitted name="pencil" size="small" />
                    </Button>
                  </ActionsPopup>
                )}
              </div>
              {/* eslint-enable jsx-a11y/click-events-have-key-events,
                                jsx-a11y/no-static-element-interactions */}
              <span className={classNames(styles.task, isCompleted && styles.taskCompleted)}>
                {name}
              </span>
            </div>
          </div>
        </NameEdit>
      </div>
    );
  },
);

Item.propTypes = {
  name: PropTypes.string.isRequired,
  approve: PropTypes.string,
  dueDate: PropTypes.instanceOf(Date),
  userId: PropTypes.string,
  userCan: PropTypes.object.isRequired, // eslint-disable-line react/forbid-prop-types
  allProjectMemberships: PropTypes.array.isRequired, // eslint-disable-line react/forbid-prop-types
  isCompleted: PropTypes.bool.isRequired,
  isPersisted: PropTypes.bool.isRequired,
  onUpdate: PropTypes.func.isRequired,
  onDelete: PropTypes.func.isRequired,
  onUserAdd: PropTypes.func.isRequired,
  onUserRemove: PropTypes.func.isRequired,
};
Item.defaultProps = {
  dueDate: undefined,
  approve: undefined,
  userId: undefined,
};
export default Item;
