import React, { useCallback, useState } from 'react';
import PropTypes from 'prop-types';
// import { useHistory } from 'react-router-dom';
import classNames from 'classnames';
import upperFirst from 'lodash/upperFirst';
import camelCase from 'lodash/camelCase';
import { Popup, Search, Container, Header, Label as SLabel, List } from 'semantic-ui-react';
import { useTranslation } from 'react-i18next';
import Label from '../Label';
import Paths from '../../constants/Paths';
import DueDate from '../DueDate';
import styles from './SearchPro.module.scss';
// import SearchPopup from '../SearchPopup/SearchPopup';

const SearchPro = React.memo(({ searchResults, onFullTextSearch }) => {
  const [t] = useTranslation();
  // const history = useHistory();
  const { isSubmitting } = searchResults;
  const [textSearch, setTextSearch] = useState(null);
  const [isOpened, setIsOpened] = useState(false);

  const handleMessage = useCallback(
    (e) => {
      if (e.target.value) {
        setTextSearch(e.target.value);
        onFullTextSearch(e.target.value);
        setIsOpened(false);
      } else {
        e.preventDefault();
        onFullTextSearch(textSearch);
        setIsOpened(true);
        setTextSearch(null);
      }
    },
    [onFullTextSearch, textSearch, setIsOpened],
  );
  const categoryLayoutRenderer = ({ categoryContent, resultsContent }) => {
    if (!resultsContent) return <div />;
    return (
      <div>
        <Header as="h3" className="name" style={{ marginLeft: 10, marginTop: 10 }}>
          {categoryContent}
        </Header>
        <Container className="results">{resultsContent}</Container>
      </div>
    );
  };
  categoryLayoutRenderer.propTypes = {
    categoryContent: PropTypes.node,
    resultsContent: PropTypes.node,
  };
  const COLORS = [
    'emerald',
    'peter-river',
    'wisteria',
    'carrot',
    'alizarin',
    'turquoise',
    'midnight-blue',
  ];

  const getColor = (name) => {
    let sum = 0;
    for (let i = 0; i < name.length; i += 1) {
      sum += name.charCodeAt(i);
    }

    return COLORS[sum % COLORS.length];
  };

  const getRouteFromRouteType = (type, result) => {
    if (type === 'projects') {
      return Paths.PROJECTS.replace(':id', result.id);
    }

    if (type === 'boards') {
      return Paths.BOARDS.replace(':id', result.id);
    }

    if (type === 'cards') {
      return Paths.CARDS.replace(':id', result.id);
    }

    if (type === 'tasks') {
      return Paths.CARDS.replace(':id', result.card_id);
    }

    if (type === 'labels') {
      return Paths.CARDS.replace(':id', result.id);
    }
    return '';
  };

  const categoryRenderer = ({ name }) => <span>{name}</span>;
  const resRender = (result) => {
    if (result.cattype === 'labels')
      return (
        <div className={styles.details}>
          {result.title}
          <span className={styles.labels}>
            <Label name={result.label_name} color={result.color} size="tiny" />
          </span>
        </div>
      );
    if (result.cattype === 'tasks')
      return (
        <div
          className={classNames(styles.task, result.is_completed && styles.taskCompleted)}
          key={result.task_id}
        >
          <div>
            {result.title}
            {result.due_date && (
              <span className={classNames(styles.attachment, styles.attachmentLeft)}>
                <DueDate value={new Date(result.due_date)} size="tiny" />
              </span>
            )}
            {result.user_name && (
              <span className={styles.labels}>
                <SLabel
                  className={classNames(
                    styles.searchUser,
                    styles[`background${upperFirst(camelCase(getColor(result.user_name)))}`],
                  )}
                  size="tiny"
                >
                  <span className={styles.initials}>{`@${result.user_name}`}</span>
                </SLabel>
              </span>
            )}
            <span className={styles.description}>{result.description}</span>
          </div>
        </div>
      );
    return (
      <div key={result.id}>
        {result.title}
        {result.due_date && (
          <span className={classNames(styles.attachment, styles.attachmentLeft)}>
            <DueDate value={new Date(result.due_date)} size="tiny" />
          </span>
        )}
        {result.user_name && (
          <span className={styles.labels}>
            <SLabel
              className={classNames(
                styles.searchUser,
                styles[`background${upperFirst(camelCase(getColor(result.user_name)))}`],
              )}
              size="tiny"
            >
              <span className={styles.initials}>{`@${result.user_name}`}</span>
            </SLabel>
          </span>
        )}
        <span className={styles.description}>{result.description}</span>
      </div>
    );
  };
  const getResults = () => {
    const results = {};
    if (isSubmitting) {
      results.loading = {
        name: t('action.search'),
        results: [{ id: '0', title: `${t('action.search')}...` }],
      };
      return results;
    }
    // tasks found
    if (searchResults.tasks.length > 0) {
      const tasks = searchResults.tasks.map((task, index) => ({
        ...task,
        key: `task-${task.id}-${index}`,
        cattype: 'tasks',
        title: task.name,
        description: `${t('common.in')} ${t('chart.card')}  [${task.card_name}]`,
      }));

      results.tasks = {
        name: t('common.tasks'),
        results: tasks,
      };
    }
    // cards found
    if (searchResults.cards.length > 0) {
      const cards = searchResults.cards.map((card, index) => ({
        ...card,
        key: `card-${card.id}-${index}`,
        cattype: 'cards',
        title: card.name,
        description: card.list_name
          ? `${t('common.in')} ${t('common.list')} [${card.list_name}] ${t('common.project')}[${
              card.project_name
            }]`
          : `${t('common.in')} ${t('common.project')} [${card.project_name}]`,
      }));

      results.cards = {
        name: t('chart.card'),
        results: cards,
      };
    }

    // projects found
    if (searchResults.projects && searchResults.projects.length > 0) {
      const projects = searchResults.projects.map((project, index) => ({
        ...project,
        key: `task-${project.id}-${index}`,
        cattype: 'projects',
        title: project.name,
      }));

      results.projects = {
        name: t('common.project'),
        results: projects,
      };
    }
    // boards found
    if (searchResults.boards.length > 0) {
      const boards = searchResults.boards.map((board, index) => ({
        ...board,
        key: `task-${board.id}-${index}`,
        cattype: 'boards',
        title: board.name,
        description: `${t('common.in')} ${t('common.project')} [${board.project_name}]`,
      }));

      results.boards = {
        name: t('common.board'),
        results: boards,
      };
    }

    // labels found
    if (searchResults.labels.length > 0) {
      const labels = searchResults.labels.map((label, index) => ({
        ...label,
        key: `task-${label.id}-${index}`,
        cattype: 'labels',
        title: label.name,
        description: `${t('common.in')} ${t('common.project')} [${label.project_name}]`,
      }));

      results.labels = {
        name: t('common.labels'),
        results: labels,
      };
    }
    return results;
  };

  const handleResultSelect = (event, { result }) => {
    const route = getRouteFromRouteType(result.cattype, result);
    if (route) {
      window.location.href = route;
      // history.push(route);
    }
  };

  return (
    <>
      <Popup
        key="searchpro"
        open={isOpened}
        trigger={
          <Search
            category
            categoryLayoutRenderer={categoryLayoutRenderer}
            categoryRenderer={categoryRenderer}
            icon="search"
            placeholder={t('action.search')}
            results={getResults()}
            resultRenderer={resRender}
            className={styles.search}
            onSearchChange={handleMessage}
            onResultSelect={handleResultSelect}
            onFocus={() => setIsOpened(textSearch == null)}
            onBlur={() => setIsOpened(false)}
            loading={isSubmitting}
          />
        }
      >
        <Popup.Header>Mẹo</Popup.Header>
        <Popup.Content>
          <span>
            Tối ưu hóa tìm kiếm của bạn với các toán tử: <br />
            <List bulleted>
              <List.Item>
                <strong>c:thẻ </strong>là chỉ tìm card
              </List.Item>
              <List.Item>
                <strong>t:việc </strong>chỉ tìm task
              </List.Item>
              <List.Item>
                <strong>b:bảng </strong>là chỉ tìm bảng
              </List.Item>
              <List.Item>
                <strong>p:project </strong>là chỉ tìm project
              </List.Item>
              <List.Item>
                <strong>@tên </strong>tên nhân viên
              </List.Item>
              <List.Item>
                <strong>#nhãn </strong>nhãn
              </List.Item>
            </List>
          </span>
        </Popup.Content>
      </Popup>
    </>
  );
});

SearchPro.propTypes = {
  // eslint-disable-next-line react/forbid-prop-types
  searchResults: PropTypes.object.isRequired,
  onFullTextSearch: PropTypes.func.isRequired,
};

export default SearchPro;
