import { call, select } from 'redux-saga/effects';

import { fetchActionsRequest } from '../requests';
import { lastActionIdByCardIdSelector, pathSelector } from '../../../selectors';

export function* fetchActionsService(cardId, type) {
  const lastId = yield select(lastActionIdByCardIdSelector, cardId);

  yield call(fetchActionsRequest, cardId, lastId, type);
}

export function* fetchActionsInCurrentCardService(type) {
  const { cardId } = yield select(pathSelector);

  yield call(fetchActionsService, cardId, type);
}
