import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import { Menu, Flag } from 'semantic-ui-react';
import i18n from '../../i18n';
// import { languages } from '../../locales';
import { withPopup } from '../../lib/popup';
import { Popup } from '../../lib/custom-ui';

import styles from './UserPopup.module.scss';

const UserStep = React.memo(({ onSettings, onLogout, onClose }) => {
  const [t] = useTranslation();

  const handleSettingsClick = useCallback(() => {
    onSettings();
    onClose();
  }, [onSettings, onClose]);

  // const [flag, setFlag] = useState(null);
  // const flags = { vi: 'vn', en: 'us' };
  // const languageOptions = languages.map((lang) => ({
  //   key: lang,
  //   text: flags[lang],
  //   value: lang,
  // }));
  const changeLanguage = (e, { name }) => {
    i18n.loadCoreLocale(name).then(() => {
      i18n.changeLanguage(name);
      // setFlag(name);
    });
  };
  // const trigger = <Flag name={flags[i18n.language] || flag || 'vn'} />;

  return (
    <>
      <Popup.Header>
        {t('common.userActions', {
          context: 'title',
        })}
      </Popup.Header>
      <Popup.Content>
        <Menu secondary vertical className={styles.menu}>
          <Menu.Item className={styles.menuItem} onClick={handleSettingsClick}>
            {t('common.settings', {
              context: 'title',
            })}
          </Menu.Item>
          <Menu.Item className={styles.menuItem}>
            <Menu.Header>{t('language.title')}</Menu.Header>
            <Menu.Menu>
              <Menu.Item name="vi" active={i18n.language === 'vi'} onClick={changeLanguage}>
                <Flag name="vn" /> {t('language.vi')}
              </Menu.Item>
              <Menu.Item name="en" active={i18n.language === 'en'} onClick={changeLanguage}>
                <Flag name="us" /> {t('language.en')}
              </Menu.Item>
            </Menu.Menu>
          </Menu.Item>
          <Menu.Item className={styles.menuItem} onClick={onLogout}>
            {t('action.logOut', {
              context: 'title',
            })}
          </Menu.Item>
        </Menu>
      </Popup.Content>
    </>
  );
});

UserStep.propTypes = {
  onSettings: PropTypes.func.isRequired,
  onLogout: PropTypes.func.isRequired,
  onClose: PropTypes.func.isRequired,
};

export default withPopup(UserStep, {
  position: 'bottom right',
});
