import React, { useCallback, useRef, useState } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { useTranslation } from 'react-i18next';
import TextareaAutosize from 'react-textarea-autosize';
import { Button, Form, TextArea } from 'semantic-ui-react';
import User from '../../User';

import { useClosableForm, useForm } from '../../../hooks';

import styles from './CommentAdd.module.scss';

const DEFAULT_DATA = {
  text: '',
};

const CommentAdd = React.memo(({ currentUser, onCreate }) => {
  const [t] = useTranslation();
  const [isEditing, toggleEdit] = useState(false);

  const [data, handleFieldChange, setData] = useForm(DEFAULT_DATA);

  const textField = useRef(null);

  // const open = useCallback(() => {
  //   toggleEdit(true);
  // }, [toggleEdit]);

  const close = useCallback(() => {
    toggleEdit(false);
  }, [toggleEdit]);

  const [handleFieldBlur, handleControlMouseOver, handleControlMouseOut] = useClosableForm(
    close,
    isEditing,
  );

  const handleFocus = useCallback(() => {
    textField.current = true;
    toggleEdit(true);
  }, [toggleEdit]);

  // const handleBlur = useCallback(() => {
  //   textField.current = false;
  //   toggleEdit(false);
  // }, [toggleEdit]);

  const submit = useCallback(() => {
    const cleanData = {
      ...data,
      text: data.text.trim(),
    };

    if (!cleanData.text) {
      textField.current.ref.current.select();
      return;
    }

    onCreate(cleanData);

    setData(DEFAULT_DATA);
  }, [onCreate, data, setData]);

  const handleFieldKeyDown = useCallback(
    (event) => {
      if (event.ctrlKey && event.key === 'Enter') {
        submit();
      }
    },
    [submit],
  );

  const handleSubmit = useCallback(() => {
    submit();
  }, [submit]);

  return (
    <Form onSubmit={handleSubmit}>
      <span className={styles.user}>
        <User name={currentUser.name} avatarUrl={currentUser.avatarUrl} />
      </span>
      <div className={classNames(styles.content)}>
        <TextArea
          ref={textField}
          as={TextareaAutosize}
          name="text"
          value={data.text}
          placeholder={t('common.writeComment')}
          minRows={1}
          spellCheck={false}
          className={styles.field}
          onFocus={handleFocus}
          onBlur={handleFieldBlur}
          onKeyDown={handleFieldKeyDown}
          onChange={handleFieldChange}
        />
        {isEditing && (
          <div className={styles.controls}>
            <Button
              positive
              content={t('action.addComment')}
              onMouseOver={handleControlMouseOver}
              onMouseOut={handleControlMouseOut}
              disabled={!data.text}
            />
          </div>
        )}
      </div>
    </Form>
  );
});

CommentAdd.propTypes = {
  currentUser: PropTypes.object.isRequired, // eslint-disable-line react/forbid-prop-types
  onCreate: PropTypes.func.isRequired,
};

export default CommentAdd;
